*{
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
  }

  ul{
    list-style: none;
  }

.nav__header ul{
    display: flex;
    flex-direction: row;
    text-decoration: none;
    justify-content: center;
    align-items: center;
    padding-left: 10px;
